.cq-chart-setting .cq-setting-container {
  height: 376px;
  width: 260px;
  position: relative;
  transition: height 250ms ease-out; }
  .cq-chart-setting .cq-setting-container.container-language {
    height: 320px; }
    .cq-chart-setting .cq-setting-container.container-language .title-text {
