  .cq-chart-setting .cq-setting-container .cq-setting-main {
    transform: translateX(-100%); }
    .cq-chart-setting .cq-setting-container .cq-setting-main.animate-enter-active, .cq-chart-setting .cq-setting-container .cq-setting-main.animate-enter-done {
