  .cq-chart-setting .cq-setting-container .cq-setting-language {
    transform: translateX(100%); }
    .cq-chart-setting .cq-setting-container .cq-setting-language.animate-enter-active, .cq-chart-setting .cq-setting-container .cq-setting-language.animate-enter-done {
