    .smartcharts-dark .ic-icon.ciq-favorite svg, .smartcharts-dark .ic-icon.ic-favorite svg, .smartcharts-dark .cq-text-icon.ciq-favorite svg, .smartcharts-dark .cq-text-icon.ic-favorite svg {
