  .smartcharts-light .ic-icon.ciq-active-favorite svg, .smartcharts-light .cq-text-icon.ciq-active-favorite svg {
    fill: #ff9933 !important;
    stroke: #ff9933 !important; }
  .smartcharts-dark .ic-icon.ciq-active-favorite svg, .smartcharts-dark .cq-text-icon.ciq-active-favorite svg {
